
.header {
    background-color: #007bff; /* Màu nền của header */
    color: #fff; /* Màu chữ */
    padding: 10px 20px; /* Khoảng cách padding */
  
    /* Căn giữa nội dung của header */
    display: flex;
    justify-content: center;
  }
  
  .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%; /* Đảm bảo header-content chiếm toàn bộ chiều rộng của header */
  }
  
  .left,
  .center,
  .right {
    display: flex;
    align-items: center;
  }
  
  .center {
    justify-content: center;
    flex-grow: 1; /* Chiếm hết không gian còn lại */
  }
  
  .icon {
    font-size: 1.5rem; /* Kích thước icon */
    cursor: pointer; /* Đổi con trỏ khi hover vào icon */
    margin: 0 10px; /* Khoảng cách giữa icon và nội dung xung quanh */
  }
  